import React from 'react'
import { Helmet } from 'react-helmet'

const SEO = ({
  title,
  description,
  type,
  slug,
  canonicalUrl,
  image,
  noindex,
  nofollow,
  schemaData,
  category,
  reviewsAndRatings,
  price,
}) => {
  const index =
    noindex || process.env.GATSBY_BUILD_ENVIRONMENT !== 'production'
      ? 'noindex'
      : 'index'
  const follow =
    nofollow || process.env.GATSBY_BUILD_ENVIRONMENT !== 'production'
      ? 'nofollow'
      : 'follow'
  const userReviews =
    schemaData &&
    reviewsAndRatings &&
    reviewsAndRatings.reviews.map(review => {
      return {
        '@type': 'Review',
        author: review.author,
        datePublished: review.dateCreated,
        description: review.body,
        reviewRating: {
          '@type': 'Rating',
          bestRating: '5',
          ratingValue: `${review.rating}`,
          worstRating: '1',
        },
      }
    })
  return (
    <Helmet>
      <title>{title}</title>
      <meta name='robots' content={`${index}, ${follow}`} />
      <meta name='description' content={description || ''} />
      <meta property='og:title' content={title || ''} />
      <meta property='og:type' content={type || ''} />
      <meta property='og:url' content={`https://www.onequince.com/${slug}`} />
      <meta property='og:image' content={image && image} />
      <link
        rel='alternate'
        href={`https://www.onequince.com/${slug}`}
        hrefLang='en-US'
      />
      <link
        rel='canonical'
        href={canonicalUrl || `https://www.onequince.com/${slug}`}
      />
      {schemaData && reviewsAndRatings && (
        <script type='application/ld+json'>
          {`{
              "@context": "https://schema.org",
              "@type": "${type}",
              "name": "${title}",
              "description": "${description}",
              "image": "${image}",
              "category": "${category}",
              "brand": "Quince",
              "offers": {
                "@type": "Offer",
                "availability": "http://schema.org/InStock",
                "price": "${price && price.toFixed(2)}",
                "priceCurrency": "USD"
              },
              "aggregateRating": {
                "@type" : "AggregateRating",
                "ratingValue": "${reviewsAndRatings.averageRating}",
                "reviewCount": "${reviewsAndRatings.count}"
              },
              "review": ${JSON.stringify(userReviews)}
            }`}
        </script>
      )}
      {schemaData && !reviewsAndRatings && (
        <script type='application/ld+json'>
          {`{
              "@context": "https://schema.org",
              "@type": "${type}",
              "name": "${title}",
              "description": "${description}",
              "image": "${image}",
              "category": "${category}",
              "brand": "Quince",
              "offers": {
                "@type": "Offer",
                "availability": "http://schema.org/InStock",
                "price": "${price && price.toFixed(2)}",
                "priceCurrency": "USD"
              }
            }`}
        </script>
      )}
    </Helmet>
  )
}

export default SEO
