import React, { useState, useRef, useEffect } from 'react'
import { graphql } from 'gatsby'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

// Styles
import styled from 'styled-components'
import { desktopVw } from '../styles/utils'

// Components
import SEO from '../components/SEO'
import ScaledImage from '../components/ScaledImage'
import RichText from '../components/RichText'
import TextButton from '../components/TextButton'
import Accordion from '../components/Accordion'

const ImageWrapper = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: -${props => desktopVw(props.theme.header.desktop.height + 2)};

    height: ${desktopVw(402)};
    box-sizing: border-box;
    position: relative;
    padding: ${desktopVw(120)} ${desktopVw(80)} ${desktopVw(35)};
    display: flex;
    justify-content: center;
    align-items: center;
  }
`

const MobileTitle = styled.h1`
  z-index: 1;
  color: ${props => props.theme.colors.primary};
  text-align: center;
  text-transform: uppercase;
  font-size: ${props => (props.fontSize ? props.fontSize : '66')}px;
  line-height: 0.8;
  font-weight: 500;
  font-family: ${props => props.theme.fonts.secondary};
  margin-top: 30px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`
const ImageTitle = styled.h1`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-weight: 500;
    font-family: ${props => props.theme.fonts.secondary};
    text-transform: uppercase;
    text-align: center;
    color: ${props => props.theme.colors.secondary};
    margin-top: 0;
    line-height: 0.8;
    font-size: ${props =>
      props.customFontSize
        ? desktopVw(props.customFontSize)
        : props.fontSize
        ? desktopVw(150)
        : desktopVw(220)};
  }
`

const Content = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(89)} ${desktopVw(80)};
  }
`

const TabsWrapper = styled.div`
  position: relative;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const NavBar = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(160)} 0 0;
    width: ${desktopVw(240)};
    height: 100%;
    position: sticky;
    top: calc(
      ${props => props.theme.announcementBar.desktop.height}px +
        ${props => props.theme.header.desktop.height}px
    );
  }
`

const RichTextWrapper = styled.div`
  box-sizing: border-box;
`

const RichTextContainer = styled.div``

const ContactDetails = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    white-space: pre-wrap;
    text-transform: uppercase;
    border-top: 1px solid black;
    font-size: ${desktopVw(10)};
    padding-top: ${desktopVw(10)};
    display: block;
  }
`
const StyledTabs = styled.div`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-direction: row;
  }
`

const NavList = styled.ul`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-bottom: ${desktopVw(25)};
  }
`

const NavItem = styled.li``

const StyledTextButton = styled(TextButton)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(16)};
    line-height: ${desktopVw(25)};
    border: none;
    height: auto;
    width: auto;
    background-image: ${props =>
      `linear-gradient(${props.theme.colors.primary}, ${props.theme.colors.primary});`};
  }
`

const StyledRichText = styled(RichText)`
  padding: 20px 10px;

  p {
    font-size: 12px;
    margin-bottom: 21px;
    white-space: pre-line;
    line-height: 19px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(480)};
    padding: 0;

    p {
      font-size: ${desktopVw(12)};
      margin-bottom: ${desktopVw(39)};
      line-height: ${desktopVw(20)};
    }
  }
`

const AccordionMobile = styled.div`
  display: block;
  padding: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const StyledAccordion = styled(Accordion)`
  .accordion-label-container h2 {
    font-size: 16px;
    padding: 0 10px;
  }

  button {
    display: none;
  }
  .accordion-item {
    padding: 0 10px;
  }
`

const FaqPage = ({ data, location }) => {
  const page = data.contentfulFaqPage
  const { accordion } = page
  const [active, setActive] = useState(location?.state?.activeAccordion ?? 0)

  const ref = useRef([])

  const vwToPx = value => {
    const viewWidth =
      window.innerWidth ||
      document.documentElement.clientWidth ||
      document.getElementsByTagName('body')[0].clientWidth
    const result = (viewWidth * value) / 100
    return result
  }

  const scrollTo = (ref, i) => {
    const menuHeight = vwToPx(5.486)
    const bodyRect = document.body.getBoundingClientRect()
    const elRect = ref.current[i].getBoundingClientRect()
    const offset = elRect.top - bodyRect.top - menuHeight

    window.scrollTo({
      top: offset,
      behavior: 'smooth',
    })
  }

  useEffect(() => {
    if (location?.state?.activeAccordion) {
      scrollTo(ref, location?.state?.activeAccordion)
      setActive(location?.state?.activeAccordion)
    }
  }, [])

  return (
    <>
      <SEO
        title={page.seoTitle}
        description={page.seoDescription}
        type={page.pageType}
        slug={page.slug}
        image={page.metaImage ? page.metaImage.fluid.src : null}
      />
      <ImageWrapper>
        <ScaledImage
          style={{
            position: 'absolute',
            left: '0',
            right: '0',
            bottom: '0',
            top: '0',
            zIndex: '-1',
          }}
          fluid={page.image.fluid}
        />
        <ImageTitle
          fontSize={page.title.length > 8 ? 1 : 0}
          fontSizeDesktop={page.fontSizeDesktop}
        >
          {page.title}
        </ImageTitle>
      </ImageWrapper>
      <MobileTitle fontSize={page.fontSizeTitle}>{page.title}</MobileTitle>
      <Content>
        {accordion && (
          <TabsWrapper>
            <StyledTabs>
              <NavBar>
                <NavList>
                  {accordion.map(({ id, title }, i) => (
                    <NavItem key={id}>
                      <StyledTextButton
                        onClick={() => {
                          setActive(i)
                          scrollTo(ref, i)
                        }}
                        asAnchor
                        active={active === i}
                      >
                        {title}
                      </StyledTextButton>
                    </NavItem>
                  ))}
                </NavList>
                <ContactDetails>
                  {documentToReactComponents(page.contact.json)}
                </ContactDetails>
              </NavBar>
              <RichTextContainer>
                {accordion.map(({ id, text }, index) => (
                  <RichTextWrapper
                    key={id}
                    ref={el => (ref.current[index] = el)}
                  >
                    <StyledRichText key={id} json={text.json} />
                  </RichTextWrapper>
                ))}
              </RichTextContainer>
            </StyledTabs>
          </TabsWrapper>
        )}
        {accordion && (
          <AccordionMobile>
            <StyledAccordion
              items={accordion}
              activeIndex={location?.state?.activeAccordion ?? 0}
              toggleMode
            />
          </AccordionMobile>
        )}
      </Content>
    </>
  )
}

export default FaqPage

export const FaqPageQuery = graphql`
  query FaqPage($id: String!) {
    contentfulFaqPage(id: { eq: $id }) {
      id
      seoTitle
      seoDescription
      metaImage {
        fluid(maxWidth: 1200) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      slug
      title
      fontSizeTitle
      fontSizeTitleDesktop
      image {
        description
        fluid(maxWidth: 3840) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      accordion {
        id
        title
        text {
          json
        }
      }
      contact {
        json
      }
    }
  }
`
